
.App {
  text-align: left;
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  background: #fe3;
  color: #123;
}

.App h2 {
  margin: 0;
}

.App p {
  margin-bottom: 0.5em;
}

.curr-time {
  padding-left: 20px;
  position: absolute;
  bottom: 20px; left: 0;
}

.quotation {
  position: absolute;
  top: 20px; left: 20px; 
  line-height: 1.5;
  font-size: 1.2em;
}

.banner {
  color: #123;
  font-size: 10vw;
  font-weight: bold;
  position: absolute;
  display: block;
  top: 50%; width: 97vw;
  padding-left: 20px;
  margin-top: -10vw;
  line-height: 2;
  overflow: visible;
}

.banner.highlight {
  color: #60e;
  text-decoration: underline;
}

.demo, .add {
  position: absolute;
  top: 0; right: 0; left: auto; padding: 20px;
  color: #fff;
  background: #90f;
  font-weight: bold;
  font-size: 20px;
  clear: both;
  user-select: none;
  cursor: pointer;
}

.demo.active {
  left: 0;
  background: rgba(0,0,0,.3);
}

.demo .close {
  float: right;
}

.demo .message {
  display: inline-block;
  width: 75vw;
}


.add {
  top: auto; bottom: 0;
  background: transparent;
  color: #90f;
  font-size: 4em;
}

.img {
  position: absolute;
  bottom: 20px; right: 20px; width: 16vw; height: 16vw; min-width: 100px; min-height: 100px;
  background: url("./bird.png") 0 0;
  background-size: contain;
}